import React from 'react'
import { connect } from 'react-redux'
import { Sidenav } from '../../sidenav.js'
import { Header } from '../../header.js'
import { Footer } from "../../footer.jsx"
import { DropdownItem } from "../../dropdown.js"
import { Slideshow } from "../../slideshow.js"
import '../../css/page.css'
import placeholder from '../../assets/images/placeholder.png'

export default class Heuristicas extends React.Component {

  render() {
    return (
    <div className="page">
    <Header/>
    <Sidenav/>
    <div className="page-content">
      <h1>Heursticas</h1>

      <div className="page-section">
        <p>
          A veces, nos encontramos con problemas que son demasiado difciles de resolver con exactitud, incluso para un computador. En estos casos, es posible que en vez de buscar la mejor solucin posible, nos conformemos con encontrar una solucin <em>suficientemente</em> buena.
        </p>
        <p>
          Una <strong>heurstica</strong> es un mtodo para aproximar el costo o beneficio de realizar una accin, y as guiar una bsqueda.
        </p>
        <p>
          Por ejemplo, si estamos intentando determinar la longitud de una carretera para realizar una bsqueda en un mapa, sera casi imposible determinar la medida exacta de la carretera, que puede tener curvas y cambios de elevacin. Pero, para estimar su longitud, podemos dibujar una lnea recta entre las dos ciudades que conecta la carretera, y considerar el largo de esta lnea como el largo de la carretera.
        </p>
        <img src={placeholder} className="page-img" alt="pic"></img>
        <p>
          En este caso, la heurstica sera <em>"Distancia en lnea recta entre dos ciudades"</em>.
        </p>
        <p>Una heurstica no necesita ser precisa, pero para poder encontrar soluciones buenas con algoritmos de bsqueda, debemos intentar encontrar la heurstica ms apropiada para el problema particular que estamos intentando solucionar.</p>
      </div>
      <div className="page-section">
        <h2>Heursticas Admisibles</h2>
        <p>
          Una heurstica <em>admisible</em> es una que nos proporcionar soluciones tiles para nuestro problema. Para que una heurstica se considere admisible, debe cumplir una de estas condiciones, segn la forma en que modelamos el problema:
          <ul>
            <li>Si modelamos la situacin como un <a href="/conceptos/arboles">rbol</a>, entonces la estimacin que nos entrega la heurstica debe ser menor o igual que el valor real. En el ejemplo anterior, sabemos que la distancia en lnea recta es una heurstica admisible porque es la distancia mnima entre dos puntos. Si la carretera real fuera una lnea recta, entonces la estimacin  sera igual al valor real, pero si la carretera real tuviera cualquier tipo de curvatura, entonces la estimacin ser siempre menor al valor real.</li>
            <li>
              Si modelamos la situacin como cualquier otro tipo de <a href="/conceptos/grafos">grafo</a>, entonces la heurstica debe cumplir una condicin llamada <em>consistencia</em>: Si tenemos un nodo <em>B</em>, al que podemos llegar directamente desde un nodo <em>A</em>, entonces el costo estimado de <em>A</em> debe ser menor o igual al costo de moverse desde <em>A</em> hacia <em>B</em>, ms el costo estimado de <em>B</em>.
            </li>
          </ul>
          <img src={placeholder} className="page-img" alt="pic"></img>
        </p>
        <p>
          El uso de heursticas es lo que diferencia a las <a href="/busqueda-no-informada">bsquedas no informadas</a> de las <a href="/busqueda-informada">bsquedas informadas</a>. Las bsquedas informadas son las que hacen uso de heursticas, y las no informadas son las que no las utilizan.
        </p>
      </div>
      <Footer prevName="rboles" prevURL="/conceptos/arboles" nextName="Bsqueda No Informada" nextURL="/busqueda-no-informada"/>
    </div>
    </div>
    );
  }
}

function mapStateToProps (state) {
  return state;
}

//export default connect(mapStateToProps)(Heuristicas)
