import React from 'react'
import { connect } from 'react-redux'
import { Sidenav } from '../../sidenav.js'
import { Header } from '../../header.js'
import { Footer } from "../../footer.jsx"
import { DropdownItem } from "../../dropdown.js"
import { Slideshow } from "../../slideshow.js"
import '../../css/page.css'
import placeholder from '../../assets/images/placeholder.png'

export default class Arboles extends React.Component {

  render() {
    return (
    <div className="page">
    <Header/>
    <Sidenav/>
    <div className="page-content">
      <h1>rboles</h1>

      <div className="page-section">
      <p> 
        Un <strong>rbol</strong> es un tipo de grafo con una estructura especial. Como indica su nombre, se asemeja a las ramas de un rbol:
      </p>
      <img src={placeholder} className="page-img" alt="pic"></img>
      <p>
        Un rbol es un grafo sin cilos, es decir, no podemos formar ninguna figura cerrada con sus aristas. Los siguientes grafos tienen ciclos, y por lo tanto <strong>no son rboles</strong>:
      </p>
      <img src={placeholder} className="page-img" alt="pic"></img>
      <p>Un rbol tambin puede ser un grafo dirigido, en el que uno de los nodos ser llamado raz. Todas las aristas apuntarn hacia la raz, o todas apuntarn en la direccin contraria a la raz.</p>
      <img src={placeholder} className="page-img" alt="pic"></img>
      <p>
        En un rbol con raz, un nodo se denomina <em>padre</em> de los nodos que descienden de l, es decir, si un nodo llamado A tiene una arista que empieza en l y termina en un nodo B, entonces A es padre de B, y B es hijo de A.
      </p>
      <img src={placeholder} className="page-img" alt="pic"></img>
      <p>
        Los rboles se utilizan para representar jerarquas o descendencia, por ejemplo en rboles genealgicos:
      </p>
      <img src={placeholder} className="page-img" alt="pic"></img>
      <p>Tambin podemos utilizarlos para representar un proceso de toma de decisiones:</p>
      <img src={placeholder} className="page-img" alt="pic"></img>
      </div>
      
      <Footer prevName="Grafos Dirigidos" prevURL="/conceptos/grafos-dirigidos" nextName="Heursticas" nextURL="/conceptos/heuristicas"/>
    </div>
    </div>
    );
  }
}

function mapStateToProps (state) {
  return state;
}

