import React from 'react'
import { connect } from 'react-redux'
import { Sidenav } from '../../sidenav.js'
import { Header } from '../../header.js'
import { Footer } from "../../footer.jsx"
import { DropdownItem } from "../../dropdown.js"
import { Slideshow } from "../../slideshow.js"
import '../../css/page.css'
import placeholder from '../../assets/images/placeholder.png'

export default class Grafos extends React.Component {

  render() {
    return (
    <div className="page">
    <Header/>
    <Sidenav/>
    <div className="page-content">
      <h1>Grafos</h1>

      <div className="page-section">
        <p>Cuando queremos que un computador resuelva un problema, necesitamos describir la situacin de una forma que el computador pueda entender, y que sea simple de explicar para nosotros. 
        Imagina que quisieras, por ejemplo, usar un computador para encontrar el camino ms corto entre dos ciudades de un mapa como este:</p>
        <img src={placeholder} className="page-img" alt="pic"></img>
        <p>Cunto trabajo sera darle al computador una descripcin completa de cada centmetro de todas las carreteras? 
        Y cunta memoria necesitara el computador para almacenar y leer toda esta informacin?</p>
        <p>
        Para ayudarnos en la tarea de resolver problemas creamos <strong>modelos</strong>; versiones simplificadas de una situacin que contienen solamente la informacin estrictamente necesaria para resolver un problema.
        </p>
        <p>Volviendo al ejemplo de las carreteras, si pensamos en la pregunta <em>Cul es el camino ms corto entre dos ciudades?</em> la nica informacin que necesitamos conocer es:</p>
        <ul>
          <li>La longitud de las carreteras, porque estamos calculando distancias.</li>
          <li>A qu ciudades est conectada cada carretera, para saber cules nos sirven para llegar al objetivo.</li>
        </ul>
        <p>No necesitamos conocer qu objetos hay en la carretera, ni siquiera si es recta o si tiene curvas. 
        La longitud es el nico dato que necesitamos para calcular distancia. Por eso, podramos representar el mapa anterior de la siguiente forma:</p>
        <img src={placeholder} className="page-img" alt="pic"></img>
        <p>As, por ejemplo, podemos calcular la longitud de este camino:</p>
        <img src={placeholder} className="page-img" alt="pic"></img>
        <p>Este tipo de modelo, donde los puntos representan lugares a los que podemos llegar, y las lneas representan conexiones entre estos lugares, 
        se llama <strong><em>grafo</em></strong>, y podemos utilizarlo para simplificar muchos tipos de problemas.</p>
      </div>

      <div className="page-section">
        <h2>Definicin de Grafo</h2>
        <p>Un grafo es un conjunto de puntos, llamados <em>nodos</em> o <em>vrtices</em>, conectados por un conjunto de lneas, llamadas <em>aristas</em>.</p>
      </div>
      <div className="page-section">
        <h2>Aplicaciones</h2>
        <p>Los grafos pueden utilizarse para representar una variedad de situaciones, por ejemplo:</p>
        <h3>Redes de Internet</h3>
        <img src={placeholder} className="page-img" alt="pic"></img>
        <h3>Mapas</h3>
        <img src={placeholder} className="page-img" alt="pic"></img>
        <h3>rboles genealgicos</h3>
        <img src={placeholder} className="page-img" alt="pic"></img>
        <p>Los mtodos de bsqueda ms utilizados en inteligencia artificial fueron creados para utilizarse en grafos, 
        por lo que es importante conocerlos para entender cmo funcionan estos mtodos.</p>
      </div>
      <div className="page-section">
        <h2>Tipos de grafos</h2>
        <p>Para poder representar ciertos tipos de problemas de forma ms apropiada, existen varios tipos de grafos con caractersticas especiales. Algunos de los ms comunes son:</p>
        <ul>
          <li><a href="/">Grafos dirigidos</a> o <em>digrafos</em>, cuyas aristas representan una conexin que solamente tiene 1 direccin.</li>
          <li><a href="/">Grafos con peso</a>, donde cada arista tiene un valor, llamado peso, que representa alguna informacin sobre la conexin.</li>
          <li><a href="/">rboles</a>, grafos que tienen 1 nodo raz y una estructura jerrquica, como las ramas de un rbol.</li>
        </ul>
      </div>
      
      <Footer prevName="Algoritmos" prevURL="/conceptos/algoritmos" nextName="Grafos no dirigidos" nextURL="/conceptos/grafos-no-dirigidos"/>
    </div>
    </div>
    );
  }
}

function mapStateToProps (state) {
  return state;
}

